/* heights and weight of web app */
.h-screen {
    height: 100vh;
}
.h-50 {
    height: 50%;
}
.w-screen {
    width: 100vw;
}
.w-100{
    width: 100%;
}
.w-50 {
    width: 50%;
}

/* flex direction */
.flex {
    display: flex;
}
.flex-col {
    flex-direction: column;
}
.justify-center {
    justify-content: center;
}
.justify-between {
    justify-content: space-between;
}
.justify-end {
    justify-content: flex-end;
}
.items-center {
    align-items: center;
}
.items-end {
    align-items: end;
}
.gap-1{
    gap: 10px;
}
.gap-2{
    gap: 20px;
}
.gap-3{
    gap: 30px;
}

/* Our Css for margins and Paddings */
.m-1 {
    margin: 10px;
}
.m-1 {
    margin: 20px;
}
.m-1 {
    margin: 30px;
}
.p-1 {
    padding: 10px;
}
.p-2 {
    padding: 20px;
}
.p-3 {
    padding: 30px;
}

