Input, select, textarea{
    /* border: 1px solid rgb(86, 86, 86); */
    padding: 5px 10px;
    width: 100%;
    border-radius: 5px;
}
.input{
  padding: 5px 10px;
  width: 70%;;
  margin-right: 10px;
}
input:focus, select:focus, textarea:focus{
    border: 1px solid rgb(34, 34, 34);
    outline: none;
}
.btn-submit {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    padding: 0.6rem 1.24rem;
    border-radius: 5px;
    border: 1px solid #5960e6;
    background: #5960e6;
    color: #ffffff;
    width: 100%;
    text-transform: uppercase;
  }
  .btn-submit:hover {
    background: #3e45ce;
  }
  .auth-multi-layout {
    position: relative;
    width: 100%;
    min-height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: var(--bg-auth);
  }
  .auth-box {
    width: 400px;
    background: #ffffff;
    border-radius: 5px;
    box-shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.25);
  }
  .auth-header {
    padding: 1.4rem 1.5rem 0.5rem 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .auth-header-logo-img {
    width: 100px;
  }
  .forgot-img .auth-header-logo-img {
    width: 200px;
  }
  .auth-header-title {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 0;
    margin-top: 10px;
    color: #212529;
  }
  .auth-header-subtitle {
    font-size: 15px;
    margin-bottom: 0;
    color: #6c757d;
  }
  .auth-body {
    padding: 0.5rem 1.5rem 1.5rem 1.5rem;
  }