 .layout{
    display: flex;
    margin: 5px;
    height: 100vh;
    gap: 15px;
}
.sidebar{
    background-color: var(--primary);
    padding: 15px;
    border-radius: 3px;
    height: 100%;
    display: flex;
    align-items: center;
}
.menu{
    display: flex;
    gap: 15px;
    flex-direction: column;
    width: 100%;
}
.menu-item{
    display: flex;
    align-items: center;
    gap: 15px;
    color: white;
    padding: 3px;
    cursor: pointer;
}
.active-menu-item{
    border: 0.5px solid white;
    border-radius: 5px 10px;
}
.body{
    width: 100%;
    height: 100%;
    overflow-x: scroll !important;
    overflow-y: scroll !important;
}
.header{
    background-color: var(--primary);
    padding: 15px;
    border-radius: 3px;
   
}
.content{
    padding-top: 15px;
}