.table{
    border: 1px solid #888888;
}
.br-3{
    border-radius: 3px;
}
.card{
    background-color: white;
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.421);
}
.wrapper{
    display: flex;
    justify-content: center;
    justify-items: center;
    padding-top: 3%;
}
.user-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    width: 650px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 2px 2px -5px rgba(0,0,0,0.5);
}
.info{
    text-align: center;
}
.info h2{
    font-size: 24px;
    margin: 0;
    margin-bottom: 10px;
    font-family: bebas neue;
}
.info p{
    font-size: 14px;
    margin-bottom: 2px;
}
.info p span{
    font-weight: 900;
    margin-right: 10px;
}
@media only screen and (min-width: 768px) {
    .user-card{
        flex-direction: row;
        align-items: flex-start;
    }
    .info{
        text-align: left;
    }
}
@media(max-width: 767px){
    .wrapper{
        padding: 3%;
    }
    .info{
        margin-top: 25px;
        font-size: 25px;
    }
    .info p span{
       display: block;
       margin-bottom: 15px;
       font-size: 18px;
    }
}