:root {
    --primary: #2218A7;
    --secondary: #3E8DE3;
}

.bg-primary {
    background-color: var(--primary) !important;
}
.bg-secondary {
    background-color: var(--secondary) !important;
}
.text-primary {
    background-color: var(--primary) !important;
}
.text-secondary {
    background-color: var(--secondary) !important;
}
.text-white{
    color: #fff !important
}
.text-black{
    color: #000 !important
}
.success-bg{
    background-color: #107010 !important;
    color: white !important;
    padding: 10px;
    border-radius: 3px;
}
.error-bg{
    background-color: #ab1313 !important;
    color: white !important;
    padding: 10px;
    border-radius: 3px;
}

